'use client'

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Button } from '@/components/ui/button'
import { 
  FileText, 
  Calendar, 
  Users, 
  Tag,
  TrendingUp,
  Clock,
  CheckCircle,
  AlertCircle
} from 'lucide-react'

const stats = [
  {
    title: 'Articles',
    value: '12',
    description: 'Articles publiés',
    icon: FileText,
    trend: '+2 cette semaine',
    color: 'text-blue-600',
    bgColor: 'bg-blue-100 dark:bg-blue-900/20'
  },
  {
    title: 'Événements',
    value: '8',
    description: 'Événements à venir',
    icon: Calendar,
    trend: '+1 cette semaine',
    color: 'text-green-600',
    bgColor: 'bg-green-100 dark:bg-green-900/20'
  },
  {
    title: 'Professionnels',
    value: '45',
    description: 'Professionnels inscrits',
    icon: Users,
    trend: '+3 cette semaine',
    color: 'text-purple-600',
    bgColor: 'bg-purple-100 dark:bg-purple-900/20'
  },
  {
    title: 'Tags',
    value: '6',
    description: 'Catégories actives',
    icon: Tag,
    trend: 'Stable',
    color: 'text-orange-600',
    bgColor: 'bg-orange-100 dark:bg-orange-900/20'
  }
]

const recentActivities = [
  {
    id: 1,
    action: 'Nouvel article créé',
    description: 'Formation aux gestes d\'urgence',
    time: 'Il y a 2 heures',
    type: 'article',
    icon: FileText,
    color: 'text-blue-600'
  },
  {
    id: 2,
    action: 'Événement programmé',
    description: 'Réunion du bureau - 15 janvier',
    time: 'Il y a 4 heures',
    type: 'event',
    icon: Calendar,
    color: 'text-green-600'
  },
  {
    id: 3,
    action: 'Nouveau professionnel',
    description: 'Dr. Martin - Cardiologue',
    time: 'Il y a 6 heures',
    type: 'professional',
    icon: Users,
    color: 'text-purple-600'
  },
  {
    id: 4,
    action: 'Tag mis à jour',
    description: 'Catégorie "Formation" modifiée',
    time: 'Il y a 1 jour',
    type: 'tag',
    icon: Tag,
    color: 'text-orange-600'
  }
]

export function DashboardOverview() {
  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between">
        <div>
          <h1 className="text-2xl font-bold text-gray-900 dark:text-white">
            Vue d'ensemble
          </h1>
          <p className="text-gray-600 dark:text-gray-300 mt-1">
            Bienvenue sur le dashboard de la CPTS des Mauges
          </p>
        </div>
        <div className="mt-4 sm:mt-0">
          <Button className="w-full sm:w-auto">
            <TrendingUp className="mr-2 h-4 w-4" />
            Voir les statistiques
          </Button>
        </div>
      </div>

      {/* Stats Grid */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-4">
        {stats.map((stat) => (
          <Card key={stat.title} className="hover:shadow-md transition-shadow">
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium text-gray-600 dark:text-gray-300">
                {stat.title}
              </CardTitle>
              <div className={`p-2 rounded-lg ${stat.bgColor}`}>
                <stat.icon className={`h-4 w-4 ${stat.color}`} />
              </div>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-gray-900 dark:text-white">
                {stat.value}
              </div>
              <p className="text-xs text-gray-500 dark:text-gray-400 mt-1">
                {stat.description}
              </p>
              <div className="flex items-center mt-2">
                <Badge variant="secondary" className="text-xs">
                  {stat.trend}
                </Badge>
              </div>
            </CardContent>
          </Card>
        ))}
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Recent Activities */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center">
              <Clock className="mr-2 h-5 w-5" />
              Activité récente
            </CardTitle>
            <CardDescription>
              Dernières actions effectuées sur le site
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            {recentActivities.map((activity) => (
              <div key={activity.id} className="flex items-start space-x-3">
                <div className={`p-2 rounded-lg bg-gray-100 dark:bg-gray-700`}>
                  <activity.icon className={`h-4 w-4 ${activity.color}`} />
                </div>
                <div className="flex-1 min-w-0">
                  <p className="text-sm font-medium text-gray-900 dark:text-white">
                    {activity.action}
                  </p>
                  <p className="text-sm text-gray-500 dark:text-gray-400">
                    {activity.description}
                  </p>
                  <p className="text-xs text-gray-400 dark:text-gray-500 mt-1">
                    {activity.time}
                  </p>
                </div>
              </div>
            ))}
          </CardContent>
        </Card>

        {/* Quick Actions */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center">
              <CheckCircle className="mr-2 h-5 w-5" />
              Actions rapides
            </CardTitle>
            <CardDescription>
              Accès direct aux fonctionnalités principales
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-3">
            <Button variant="outline" className="w-full justify-start">
              <FileText className="mr-2 h-4 w-4" />
              Créer un article
            </Button>
            <Button variant="outline" className="w-full justify-start">
              <Calendar className="mr-2 h-4 w-4" />
              Programmer un événement
            </Button>
            <Button variant="outline" className="w-full justify-start">
              <Users className="mr-2 h-4 w-4" />
              Ajouter un professionnel
            </Button>
            <Button variant="outline" className="w-full justify-start">
              <Tag className="mr-2 h-4 w-4" />
              Gérer les tags
            </Button>
          </CardContent>
        </Card>
      </div>

      {/* System Status */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center">
            <AlertCircle className="mr-2 h-5 w-5" />
            État du système
          </CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 sm:grid-cols-3 gap-4">
            <div className="flex items-center space-x-2">
              <div className="w-2 h-2 bg-green-500 rounded-full"></div>
              <span className="text-sm text-gray-600 dark:text-gray-300">
                Base de données
              </span>
            </div>
            <div className="flex items-center space-x-2">
              <div className="w-2 h-2 bg-green-500 rounded-full"></div>
              <span className="text-sm text-gray-600 dark:text-gray-300">
                API
              </span>
            </div>
            <div className="flex items-center space-x-2">
              <div className="w-2 h-2 bg-green-500 rounded-full"></div>
              <span className="text-sm text-gray-600 dark:text-gray-300">
                Site web
              </span>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
